import React, { Component, Fragment } from "react";
import { Query } from "react-apollo";
import gql from "graphql-tag";
import styled from "styled-components";
import Woman from "./Woman";
import CreateWoman from "./CreateWoman";
import ThreeWomen from "./ThreeWomen";
import { WomenList } from "./WomenList";

const ALL_WOMEN_QUERY = gql`
  query ALL_WOMEN_QUERY {
    women {
      id
      name
      description
    }
  }
`;

const Center = styled.div`
  position: relative;
  width: 100vw;
`;

class Women extends Component {
  state = {
    x: null,
    y: null
  };
  componentDidUpdate() {}

  _onMouseMove = e => {
    this.setState({ x: e.screenX, y: e.screenY });
  };

  calcX = x => {
    if (typeof window === "undefined") {
      return 0;
    } else {
      // measure centerpoint
      const winWidth = window.innerWidth;
      const center = winWidth / 2;
      const distFromCenter = x - center;
      const decimal = distFromCenter / center;
      console.log({ x, winWidth, center, distFromCenter, decimal });
    }
  };

  render() {
    console.log(this.calcX(this.state.x));
    return (
      <Center onMouseMove={this._onMouseMove}>
        <Query query={ALL_WOMEN_QUERY} refetch={true}>
          {({ data, error, loading }) => {
            if (loading) return <p>Loading...</p>;
            if (error) return <p>Error: {error.message}</p>;
            return (
              <Fragment>
                {/* <ThreeWomen women={data.women} /> */}
                <WomenList
                  women={data.women}
                  x={this.state.x}
                  y={this.state.y}
                />
                <CreateWoman />
              </Fragment>
            );
          }}
        </Query>
      </Center>
    );
  }
}

export default Women;
export { ALL_WOMEN_QUERY };
